/* eslint-disable max-len */
import { Grid, makeStyles, Typography, Link } from "@material-ui/core";
import React from "react";
import { Scrollbars } from "react-custom-scrollbars";
import { QuickStartCheckList } from "./QuickStartCheckList";
import KatalonLogo from "../../../../assets/icon/Katalon-Logo.svg";

const useStyles = makeStyles({
  root: {
    top: "0",
    left: "0",
    width: "100%",
    height: "100%",
    margin: "0",
    background: "white",
  },
  WindowsWidth: {
    top: "0",
    left: "0",
    height: "100%",
    margin: "0",
    width: "100%",
    minWidth: "1300px",
    background: "white",
  },
  macOSWidth: {
    top: "0",
    left: "0",
    width: "100%",
    height: "100%",
    background: "white",
    minWidth: "1000px",
  },
  content: {
    margin: "0",
    width: "100%",
    marginTop: "20px",
  },
  gettingStarted: {
    fontFamily: "Inter",
    fontSize: "28px",
    fontWeight: 600,
    color: "#22283C",
    lineHeight: "36px",
    height: "36px",
    width: "217px",
  },
  welcomeMessage: {
    marginTop: "20px",
    fontFamily: "Inter",
    fontSize: "16px",
    color: "#4F5465",
    fontWeight: 500,
    lineHeight: "28px",
  },
  helpGrid: {
    marginTop: "40px",
  },
  helpMessage: {
    fontFamily: "Inter",
    fontWeight: 500,
    fontSize: "12px",
    lineHeight: "28px",
    color: "#4F5465",
  },
  logo: {
    width: "108.5px",
    height: "28px",
    marginBottom: "30px",
  },
});

export const QuickStartContent = () => {
  const classes = useStyles();

  function handleClick(event) {
    window.location.href = "https://docs.katalon.com/";
    event.preventDefault();
    event.stopImmediatePropagation();
  }

  const isWindows = navigator.platform.includes('Win')

  return (
    <Scrollbars
      autoHide
      hideTracksWhenNotNeeded
      style={{ width: "100%" }}
      autoHeightMin="100%"
      autoHeightMax="100%"
    >
      <Grid
        container
        justifyContent="center"
        alignItems="center"
        alignContent="center"
        className={classes.root | isWindows ? classes.WindowsWidth : classes.macOSWidth}
      >
        <Grid
          item
          container
          justifyContent="center"
          alignItems="center"
          spacing={4}
          xs={isWindows ? 8 : 10}
          sm={isWindows ? 8 : 11}
          className={classes.content | isWindows ? classes.WindowsWidth : classes.macOSWidth}
        >
          <Grid
            item
            xs={isWindows ? 10 : 12}
            sm={12}
            align="center"
          >
            <img
              src={KatalonLogo}
              className={classes.logo}
              alt={"Katalon_Logo"}
            />
            <Typography
              align="center"
              variant="h4"
              className={classes.gettingStarted}
            >
              Getting Started
            </Typography>
            <Typography
              align="center"
              variant="h4"
              className={classes.welcomeMessage}
            >
              The fastest way to explore the full potential of Katalon
            </Typography>
          </Grid>
          <Grid item xs={12} sm={isWindows ? 9 : 12} justifyContent="center">
            <QuickStartCheckList />
          </Grid>
          <Grid item xs={12} sm={12} className={classes.helpGrid}>
            <Typography
              variant="subtitle1"
              align="center"
              className={classes.helpMessage}
            >
              Need help?{" "}
              <Link
                href="https://docs.katalon.com/"
                style={{ color: "#6565EC" }}
                onClick={handleClick}
              >
                Start using Docs
              </Link>
            </Typography>
          </Grid>
        </Grid>
      </Grid>
    </Scrollbars>
  );
};
